*{
  font-family: Helvetica,Arial,sans-serif;  
}

body{
  padding: 60px;
}

input{
  color: gray;
  font-size: 1.3rem;
  font-weight: 500;
  width: 100%;

}

.gif{
  display: inline-block;  
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  border: 1px solid gray;
  border-radius: 5px;
  display: inline-block;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 250px;
  width: 100%;
}